.container {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;

  @include vp-1199 {
    padding: 0 45px;
  }

  @include vp-767 {
    padding: 0 15px;
  }

  &--gym {
    @include vp-767 {
      padding: 0;
    }
  }

  &--advantages {
    @include vp-767 {
      padding: 0;
    }
  }
}
